<template>
    <div class="message-center">
        <div class="msg-list">
            <div class="title">
                訊息列表
                <div class="btn-group">
                    <span @click="toggleType('my')" :class="{'active':curMessageType === 'my'}">發起</span>
                    ｜
                    <span @click="toggleType('apply')" :class="{'active':curMessageType ==='apply'}">參與</span>
                </div>
            </div>
            <div class="msg-list-item" v-for="(item,i) in curMessageTypeList" :key="i">
                <div class="my" v-if="curMessageType == 'my'" @click="selectItem(item)" :class="{'active':curItem.itemId == item.itemId}" >
                    <div class="item-name">
                        <span class="member" v-for="(member) in item.ownMember" :key="member">{{member}}</span>
                        換
                        <span class="member" v-for="(member,i) in item.targetMember" :key="i">{{member}} </span>
                    </div>
                    <p class="summary" v-if="item.desc">{{item.desc.length>20?item.desc.slice(0,30)+'...':item.desc}}</p>
                    <div class="user">建立者 {{item.creator}}</div>
                </div>
                <div class="apply" v-else @click="selectItem(item.exchangeInfo)" :class="{'active':curItem.itemId == item.exchangeInfo.itemId}" >
                    <div class="item-name">
                        <span class="member" v-for="(member) in item.exchangeInfo.ownMember" :key="member">{{member}}</span>
                        換
                        <span class="member" v-for="(member,i) in item.exchangeInfo.targetMember" :key="i">{{member}} </span>
                    </div>
                    <p class="summary" v-if="item.exchangeInfo.desc">{{item.exchangeInfo.desc.length>20?item.exchangeInfo.desc.slice(0,30)+'...':item.exchangeInfo.desc}}</p>
                    <div class="user">建立者 {{item.exchangeInfo.creator}}</div>
                </div>
            </div>
        </div>
        <div class="msg-box">
             <div class="title" v-if="curMessageList.length>0">
                {{curMessageList[0].fromUser == userName?curMessageList[0].toUser:curMessageList[0].fromUser}}
            </div>
            <div class="msg-detail-container">
                <div class="msg-detail" v-for="(msg,i) in curMessageList" :key="i" :class="{'self':msg.fromUser == userName}">
                        <div class="text">{{msg.postMessage}}</div> 
                        <div class="date">{{msg.create_time}}</div>
                </div>
                 
            </div>
            <div class="msg-send">
                    <el-input  class="type-box" autosize maxlength="100" type="textarea" show-word-limit v-model="msgText" placeholder="輸入訊息" ></el-input>
                    <el-button class="btn-send" @click="doSendMessage()">送出</el-button>   
                </div>
           
        </div>
        <div class="msg-info">
            <div class="item-img"></div>
            
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex'
import { getMyExchangeItem } from '../../api/user'; 
import { getExchangeMessagList,sendMessage } from '../../api/message'; 

export default {
    data(){
        return {
            myApplyList:[],
            myExchangeList:[],
            curItemId: "",
            curItem:{},
            msgText:"",
            curMessageType:'my',
            curMessageList: [],
            curMessageTypeList:[]
        }
    },
    computed:{
        ...mapState({
            userImg: state => state.user.userImg,
            userName: state => state.user.userName,
        }),
    },
    watch:{
    },
    async mounted(){
        await this.getUserItemList()
        this.toggleType('my')
    },
    methods:{
        getUserItemList(){
            let params = {
                userName:this.$store.state.user.userName,
                type:'exchange'
            }
            return new Promise((resolve,reject)=>{
                getMyExchangeItem(params).then(res => {
                    console.log(res);
                    this.myApplyList = res.body.myApplyList
                    this.myExchangeList = res.body.myExchangeList
                    resolve()
                    }).catch(err => {
                        console.log(err);
                    });
            })
        },
        selectItem(item){
            this.curItemId = item.itemId 
            this.curItem = item
            this.getItemMessage()
        },
        toggleType(type){
            this.curMessageType = type
            if (type == 'my'){
                this.curMessageTypeList = this.myExchangeList
                this.selectItem(this.curMessageTypeList[0])
            } else {
                this.curMessageTypeList = this.myApplyList
                this.selectItem(this.curMessageTypeList[0].exchangeInfo)
            }
        },
        getItemMessage(){
            let params = {
                itemId: this.curItemId
            }
            getExchangeMessagList(params).then(res => {
                console.log(res);
                this.curMessageList = res.messageList
                }).catch(err => {
                    console.log(err);
                });
        },
        doSendMessage(){
            let curMessageInfo = this.curMessageList[0]
            let params = {
                postMessage:this.msgText,
                fromUser:this.userName,
                toUser:curMessageInfo.fromUser !== this.userName?curMessageInfo.fromUser:curMessageInfo.toUser,
                relatedItem:curMessageInfo.relatedItem
            }
            console.log(params)
            sendMessage(params).then(res => {
                this.getItemMessage()
                this.msgText = ""
                }).catch(err => {
                    console.log(err);
                });
        }
    }
}
</script>

